<template>
    <div class="op-box">
        <div class="op-prtcl-line">
            <span class="op-prtcl">{{ $t('setop.opprotocol') }}</span>
            <div class="op-input">
                <LeetxSwitch :module="module" :height="2" :value="oldOpData.use_op" :activeVal="true" :staticVal="false"
                    v-model="opData.use_op.text"></LeetxSwitch>
            </div>
        </div>
        <div class="ip-addresss-line">
            <span class="ip-addresss">{{ $t('setop.ipaddress') }}</span>
            <InputText :maxLength="20" :module="module" class="ip-input" :text="oldOpData.ip" v-model="opData.ip">
            </InputText>
        </div>
        <div class="port-line">
            <span class="port">{{ $t('setop.portnumber') }}</span>
            <InputVal :imprprChrctr="limitNumBtn" :module="module" class="string-input"
                v-model:sensorValue="opData.port.text" :minNum="1" :maxNum="65535"></InputVal>
        </div>
        <div v-show="this.$store.state.f_status.user == 'lx'">
            <div class="server-prtcl-line">
                <span class="op-prtcl">{{ $t('setop.resultUpload') }}</span>
                <div class="op-input">
                    <LeetxSwitch :module="module" :height="2" :value="oldOpData.server_enable" :activeVal="true"
                        :staticVal="false" v-model="opData.server_enable.text"></LeetxSwitch>
                </div>
            </div>
            <div class="server-addresss-line">
                <span class="ip-addresss">{{ $t('setop.server') }}</span>
                <InputText :maxLength="20" :module="module" class="ip-input" :text="oldOpData.server_ip"
                    v-model="opData.server_ip"></InputText>
            </div>
        </div>
        <LeetxButton :enablerun="false" :module="module" class="confirm-box" textColor="#fff" bgcColor="#00c155"
            width="60.625vw" height="3.75rem" fontSize="2.25rem" fontFamily="--SHSC-B" :text="$t('confirm.confirm')"
            @click="submitOpData"></LeetxButton>
        <!-- <Confirm class="confirm-box" :text="$t('confirm.confirm')" @hasClick="submitOpData"></Confirm> -->
    </div>
</template>
<script>
import LeetxButton from '../controls/LeetxButton.vue'
import InputText from "../input/InputText.vue"
import InputVal from '@/components/input/InputVal.vue'
import LeetxSwitch from '../switch/LeetxSwitch.vue'
import { devCfgNetOpGetApi, devCfgNetOpSetApi } from '../../api/device/device'
import cnfrmWin from '../../dialogs/common/confirm/cnfrm'
import { deepCopy, hasSameStructure } from '../../utils/deepCopy'
import { messageApi } from '../message/message.js'
export default {
    components: { InputText, LeetxButton, LeetxSwitch, InputVal },

    data() {
        return {
            module: "DEV_BUS",
            oldOpData: {
                "ip": "127.0.0.1",
                "port": 8877,
                "use_op": true,
                "server_ip": '127.0.0.1',
                "server_enable": false
            },
            opData: {
                "ip": { 'text': "127.0.0.1", 'valid': true },
                "port": { 'text': 8877, 'valid': true },
                "use_op": { 'text': false, 'valid': true },
                "server_ip": { 'text': "127.0.0.1", 'valid': true },
                "server_enable": { 'text': false, 'valid': true }
            },
            isFirstChange: true,
            isAllowLeave: true,
            limitNumBtn: ['-', '.'],

        }
    },
    methods: {
        // 获取OP信息

        // 设置OP信息
        submitOpData() {
            this.isAllowLeave = true
            let data = this.getToServerData()
            devCfgNetOpSetApi(data).then((x) => {
                if (x.status == 0) {
                    messageApi.success([this.$t('setop.saveSuc')])
                }
                else {
                    switch (x.status) {
                        case 9164:
                            messageApi.warning([this.$t('setop.networkUnavailable')])
                            break;
                        case 9161:
                            messageApi.warning([this.$t('setop.portOccupied')])
                            break;
                        case 9163:
                            messageApi.warning([this.$t('setop.opRestartFail')])
                            break;
                        case 9160:
                            messageApi.warning([this.$t('setop.failChangeIpAddress')])
                            break;
                        case 9162:
                            messageApi.warning([this.$t('setop.iPAddressInvalid')])
                            break;
                        default:
                            messageApi.warning([this.$t('setop.formatError')])
                    }

                }
            }).catch(() => {
                messageApi.warning([this.$t('setop.saveErr')])
            })
        },
        initData() {
            devCfgNetOpGetApi().then((result) => {
                if (result.status == 0) {
                    this.handleServerData(result.data)
                }
            }).catch(() => {
                this.handleServerData(this.oldOpData)
            });

        },
        getToServerData() {
            let newData = {}
            for (let key in this.opData) {
                newData[key] = this.opData[key].text
            }
            return newData
        },
        handleServerData(data) {
            this.opData.ip.text = data.ip
            this.opData.port.text = data.port
            this.opData.use_op.text = data.use_op
            this.opData.server_ip.text = data.server_ip
            this.opData.server_enable.text = data.server_enable
            this.oldOpData = deepCopy(this.getToServerData())
            setTimeout(() => {
                this.isFirstChange = false
            }, 100)
        }
    },
    created() {
        this.initData()
    },
    beforeRouteLeave(to, from, next) {
        if (this.isAllowLeave || this.$store.state.g_status.busy) {
            next()
            return
        }
        let title = this.$t('setcontrol.saveSet')
        let confirmText = this.$t('setcontrol.yes')
        let cancelText = this.$t('setcontrol.no')
        let msg = this.$t('setcontrol.isSaveSet')
        cnfrmWin({
            title,
            confirmText,
            cancelText,
            msg
        }).then(() => {
            this.submitOpData()
            setTimeout(() => {
                next()
            }, 100)
        }).catch(() => {
            next()
        });
    },
    watch: {
        opData: {
            deep: true,
            handler: function () {
                if (!this.isFirstChange) {
                    this.isAllowLeave = hasSameStructure(this.oldOpData, this.getToServerData(), true)
                }
            }
        }
    }
}
</script>
<style scoped>
.op-box {
    position: relative;
    width: 100%;
    height: 100%;
}

.op-box {
    position: relative;
    height: 100%;
    width: 100%;
}

.op-nav-box {
    display: flex;
    width: 45.125vw;
    position: relative;
    left: 50%;
    height: 3.7387vh;
    line-height: 3.7387vh;
    transform: translateX(-50%);
}

.op-nav-box span {
    flex: 1;
}

.bytes-order {
    position: relative;
    top: 1rem;
}

.op-prtcl-line,
.ip-addresss-line,
.server-prtcl-line,
.server-addresss-line,
.port-line {
    position: absolute;
    height: 3.9063vh;
    width: 100%;
    top: 2.6961vh;
    color: var(--C3B);
}

.op-prtcl,
.ip-addresss,
.port {
    position: absolute;
    left: 7.4375vw;
    top: 50%;
    transform: translateY(-50%);
    font-size: var(--font1_625) !important;

}

.ip-input,
.string-input {
    position: absolute;
    right: 7.4375vw;
}

.op-input {
    position: absolute;
    right: calc(7.4375vw + 0.5rem);
    top: 50%;
    transform: translateY(-50%);
    width: 17.625rem;
    height: 2rem;

}

.ip-addresss-line {
    top: 7.7742vh;
}

.port-line {
    top: 12.8125vh;
}


.server-prtcl-line {
    top: 17.8508vh;
}


.server-addresss-line {
    top: 22.8891vh;
}

.confirm-box {
    position: absolute;
    bottom: 3.5rem;
    left: 50%;
    transform: translateX(-50%);
}

.brdrbtm {
    box-sizing: border-box;
    border-bottom: 2px solid var(--C00c155);
}
</style>